* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header-title {
  font-family: 'Roboto', sans-serif;
  width: 100vw;
  height: 100px;
  line-height: 100px;
  background: rgba(4, 59, 29, 0.9);
  color: rgb(238, 230, 183);
  text-align: center;
}

.main-container {
  display: flex;
  justify-content: center;
  background: url(./assets/cats-pattern001.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  padding: 80px;
}

.info-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  height: 300px;
  /* background: transparent; */
  background: rgba(4, 59, 29, 0.9);
  box-shadow: 0 5px 30px rgba(18, 20, 18, 0.5);
  border-radius: 10px;
  padding: 20px;
  margin-top: 70px;
}

button {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Helvetica Neue', sans-serif;
  font-size: 1.25rem;
  width: 250px;
  height: 40px;
  background: darkgray;
  border: none;
  outline: none;
  border-radius: 5px;
  color: rgb(18, 26, 4);
  box-shadow: 1px 1px 5px lightgray;
  cursor: pointer;
}

button:active {
  box-shadow: 1px 1px 10px rgb(86, 88, 86);
}
p {
  font-family: Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
  padding: 10px 30px;
  color: white;
}

.attributions {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 100vw;
  height: max-content;
  padding: 10px;
  background: rgb(9, 46, 46);
}

.attributions a {
  width: 100vw;
  font-size: 12px;
  font-family: "Roboto", sans-serif;
  padding: 0 40px;
  text-align: center;
  color: rgb(87, 151, 211);
}
